<template>
  <div>
    <el-drawer
      title=""
      :visible.sync="drawer"
      :close-on-press-escape="false"
      custom-class="mappingBox"
      :wrapperClosable="closable"
      :show-close="showClose"
      :direction="direction"
      @close="closeDrawer"
      >
      
      <div class="authorCom">
        <div class="editItem flex">
          <el-button native-type="button" class="editMapBtn" @click="newMappingBtn"></el-button>
        </div>
        <div class="editItem flex">
          <el-button native-type="button" class="editMapBtn" @click="continueMappingBtn"></el-button>
        </div>
      </div>
    </el-drawer>

    <div class="mappingSaveClose flex" v-show="mappingEnd">
      <el-button type="primary" icon="el-icon-check" circle @click="saveMapContinue"></el-button>
      <el-button type="warning" icon="el-icon-close" circle @click="saveMapClose"></el-button>
    </div>
  </div>
</template>

<script>
import { displayMap, newMapping, continueMapping, showNewMap, submitSaveMapContinue, submitSaveMapBtn } from "@/js/map";
export default {
  name: "mapping",
  components: {
    
  },
  data() {
    return {
      drawer: false,
      direction: 'btt',
      closable: true,
      showClose: false,
      mapName: null,
      mappingEnd: false,
      name: "map"
    };
  },
  mounted(){
    //  
    let mappingStatus = window.localStorage.getItem('mappingStatus');
    if(mappingStatus == '1'){
      this.mappingEnd = true;
    }else{
      this.mappingEnd = false;
    }
  },

  methods: {
    //
    newMappingBtn: function(){
      this.mappingNotify();
      newMapping();
      displayMap('/map', 'map');
    },

    //
    continueMappingBtn: function(){
      this.mappingNotify();
      continueMapping();
      displayMap('/map', 'map');
    },

    //
    mappingNotify: function(){
      const that = this;
      that.$notify({
          title: '',
          message: '',
          type: 'warning',
          position: 'top-left',
          offset: 100,
          duration: 0
      });
      that.mappingEnd = true;
      showNewMap(false);
    },

    //
    saveMapContinue: function() {
      let that = this;
      if(!that.mapName){
        that.mapNamePrompt("save");
      } else{
        submitSaveMapContinue(this.mapName);
      }
    },

    //
    saveMapClose: function() {
      if(!this.mapName){
        this.mapNamePrompt("close")
      } else{
        submitSaveMapBtn(this.mapName);
      }
      this.mappingEnd = false;
    },

    //prompt
    mapNamePrompt: function(type){
      let that = this;
      that.$prompt('', '', {
        customClass: "mappingInputPrompt",
        inputValue: "map",
        confirmButtonText: '',
        cancelButtonText: '',
        inputPattern: /^\w+$/,
        inputErrorMessage: ''
      }).then(({ value }) => {
        that.$notify({
          title: '',
          message: '',
          type: 'warning',
          position: 'top-left',
          offset: 100,
          duration: 0
        });
        that.mapName = value;
        if(type === "save"){
          submitSaveMapContinue(value);
        } else{
          submitSaveMapBtn(value);
        }
      }).catch(() => {
        
      });
    },

    //
    closeDrawer: function(){
      showNewMap(false);
    }
  },

  //
  watch:{
      "$store.state.edipMapModule": function(val){
          this.drawer = val;
      }
  }
};
</script>

<style scoped lang="less">
 .skip{
    position: absolute;
    color: #409EFF;
    top: -100px;
    right: 15px;
 }
.authorCom {
  width: 100%;
  padding: 0 15px;
  button {
    background: none;
    color: #ffffff;
  }
  .editItem {
    flex: 1;
    background-color: #409eff;
    color: #ffffff;
    border-radius: 4px;
    height: 35px;
    line-height: 35px;
    margin-top: 20px;
    .editMapBtn {
      width: 100%;
      border: none;
      padding: 0;
    }
  }
  .mapNameInp {
   height: 35px;
    line-height: 35px;
  }
}
.mappingSaveClose{
  position: fixed;
  z-index: 999;
  right: 10px;
  bottom: 340px;
  width: 50px;
  flex-direction: column;
  button{
    margin-left: 0;
    margin-bottom: 20px;
  }
}
</style>


